'use client';

import Link from 'next/link';
import { useState, useEffect } from 'react';
import { Button } from '@/components/ui';
import { APP_CONFIG } from '@/lib/config/constants';
import { MenuItem } from '@/lib/types';
import type { Locale } from '@/i18n-config';
import { useCart } from '@/lib/context/CartContext';
import { useAuth } from '@/lib/context/AuthContext';
import { CompareButton } from '@/components/products/CompareButton';

interface HeaderProps {
  menuItems: MenuItem[];
  lang: Locale;
  dictionary?: {
    common: {
      home: string;
      products: string;
      about: string;
      contact: string;
    };
    auth?: {
      login: string;
      register?: string;
      my_account: string;
      my_orders?: string;
      logout: string;
    };
    compare?: {
      compare_products: string;
      items: string;
    };
  };
}

export function Header({ menuItems, lang, dictionary }: HeaderProps) {
  const { getCartCount } = useCart();
  const { isAuthenticated, user, logout } = useAuth();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  const [cartCount, setCartCount] = useState(0);
  const [mounted, setMounted] = useState(false);
  const [cartAnimating, setCartAnimating] = useState(false);
  const [prevCartCount, setPrevCartCount] = useState(0);

  // Set mounted state
  useEffect(() => {
    setMounted(true);
  }, []);

  // Update cart count when cart changes
  useEffect(() => {
    if (mounted) {
      const newCount = getCartCount();
      setCartCount(newCount);

      // Trigger animation if count increased
      if (newCount > prevCartCount) {
        setCartAnimating(true);
        setTimeout(() => setCartAnimating(false), 600);
      }
      setPrevCartCount(newCount);
    }
  }, [getCartCount, mounted, prevCartCount]);

  // Handle scroll to shrink header
  useEffect(() => {
    if (!mounted) return;

    const handleScroll = () => {
      const currentScrollY = window.scrollY;

      // Shrink header when scrolled down
      setIsScrolled(currentScrollY > 50);
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, [mounted]);

  // Close menu when clicking outside
  useEffect(() => {
    const handleClickOutside = () => {
      if (isMenuOpen) {
        setIsMenuOpen(false);
      }
    };

    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, [isMenuOpen]);

  const renderMenuItem = (item: MenuItem, isMobile: boolean = false) => {
    const baseClasses = isMobile
      ? 'text-gray-700 hover:text-blue-600 font-medium transition'
      : 'text-gray-700 hover:text-blue-600 font-medium transition relative group';

    const content = (
      <>
        {item.label}
        {!isMobile && (
          <span className="absolute -bottom-1 left-0 w-0 h-0.5 bg-blue-600 group-hover:w-full transition-all duration-300"></span>
        )}
      </>
    );

    // Use path if available, otherwise use url, and prefix with current locale
    const basePath = item.path || item.url || '#';
    const href = basePath.startsWith('/') && !basePath.startsWith(`/${lang}`)
      ? `/${lang}${basePath}`
      : basePath;

    return (
      <Link
        key={item.id}
        href={href}
        className={baseClasses}
        onClick={() => isMobile && setIsMenuOpen(false)}
      >
        {content}
      </Link>
    );
  };

  const toggleLanguage = (newLang: Locale) => {
    const currentPath = window.location.pathname;
    // Remove current language from path and add new language
    const pathWithoutLang = currentPath.replace(/^\/(en|th)/, '') || '/';
    window.location.href = `/${newLang}${pathWithoutLang}`;
  };

  return (
    <>
      {/* Primary Navigation Bar */}
      <div className="fixed top-0 left-0 right-0 z-50">
        {/* Mini Top Bar */}
        <div className="bg-gray-900 text-white py-1">
          <div className="max-w-7xl mx-auto px-6">
            <div className="flex justify-between items-center">
              {/* Phone Number */}
              <a
                href={`tel:${APP_CONFIG.phone.replace(/\s/g, '')}`}
                className="flex items-center gap-1.5 text-xs hover:text-blue-400 transition-colors"
              >
                <svg className="w-3.5 h-3.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 5a2 2 0 012-2h3.28a1 1 0 01.948.684l1.498 4.493a1 1 0 01-.502 1.21l-2.257 1.13a11.042 11.042 0 005.516 5.516l1.13-2.257a1 1 0 011.21-.502l4.493 1.498a1 1 0 01.684.949V19a2 2 0 01-2 2h-1C9.716 21 3 14.284 3 6V5z" />
                </svg>
                <span>{APP_CONFIG.phone}</span>
              </a>

              {/* Right Side: Language Switcher & Social Links */}
              <div className="flex items-center gap-3">
                {/* Language Switcher */}
                <div className="flex items-center gap-2">
                  <button
                    onClick={() => toggleLanguage('en')}
                    className={`px-2 py-0.5 text-xs font-bold transition-colors rounded ${
                      lang === 'en'
                        ? 'text-blue-400 bg-white/10'
                        : 'text-gray-300 hover:text-blue-400 hover:bg-white/10'
                    }`}
                  >
                    EN
                  </button>
                  <span className="text-gray-600">|</span>
                  <button
                    onClick={() => toggleLanguage('th')}
                    className={`px-2 py-0.5 text-xs font-bold transition-colors rounded ${
                      lang === 'th'
                        ? 'text-blue-400 bg-white/10'
                        : 'text-gray-300 hover:text-blue-400 hover:bg-white/10'
                    }`}
                  >
                    ไทย
                  </button>
                </div>

                {/* Social Links */}
                <div className="flex gap-2">
                  {[
                    {
                      name: 'Facebook',
                      icon: (
                        <svg className="w-3.5 h-3.5" fill="currentColor" viewBox="0 0 24 24">
                          <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z" />
                        </svg>
                      ),
                      href: 'https://www.facebook.com/sakwoodworks',
                      color: 'bg-blue-600 hover:bg-blue-700',
                    },
                    {
                      name: 'Line',
                      icon: (
                        <svg className="w-3.5 h-3.5" fill="currentColor" viewBox="0 0 24 24">
                          <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-2-2 2-2-2-2 2-2 2 2 2-2-2-2 2-2 2 2 2-2-2-2 2-2z" />
                        </svg>
                      ),
                      href: 'https://line.me/ti/p/@sakwoodworks',
                      color: 'bg-green-500 hover:bg-green-600',
                    },
                  ].map((social) => (
                    <a
                      key={social.name}
                      href={social.href}
                      target="_blank"
                      rel="noopener noreferrer"
                      className={`${social.color} text-white p-1 rounded-full transition-colors`}
                      aria-label={social.name}
                    >
                      {social.icon}
                    </a>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Main Header */}
        <header
          className={`bg-white/95 backdrop-blur-md shadow-sm border-b border-gray-100 transition-all duration-300 ${
            isScrolled ? 'py-2 shadow-lg' : 'py-4 shadow-sm'
          }`}
        >
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex justify-between items-center">
          {/* Logo */}
          <Link href={`/${lang}`} className="flex items-center gap-3 group">
            <div className={`bg-gradient-to-br from-blue-600 to-indigo-600 rounded-xl flex items-center justify-center group-hover:scale-110 transition-transform shadow-lg ${
              isScrolled ? 'w-8 h-8' : 'w-10 h-10'
            }`}>
              <span className={`text-white font-bold transition-all ${
                isScrolled ? 'text-lg' : 'text-xl'
              }`}>S</span>
            </div>
            <span className={`font-bold bg-gradient-to-r from-blue-700 to-indigo-700 bg-clip-text text-transparent transition-all ${
              isScrolled ? 'text-xl' : 'text-2xl'
            }`}>
              {APP_CONFIG.name}
            </span>
          </Link>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center gap-6">
            {menuItems.map((item) => renderMenuItem(item))}

            {/* Cart Icon */}
            <Link
              href={`/${lang}/cart`}
              className={`relative p-2 text-gray-700 hover:text-blue-600 transition-all ml-2 ${
                cartAnimating ? 'animate-bounce' : ''
              }`}
              aria-label="Shopping cart"
            >
              <svg className={`transition-all ${isScrolled ? 'w-5 h-5' : 'w-6 h-6'} ${
                cartAnimating ? 'animate-spin' : ''
              }`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
              </svg>
              {cartCount > 0 && (
                <span className={`absolute -top-1 -right-1 bg-red-600 text-white text-xs font-bold rounded-full w-5 h-5 flex items-center justify-center transition-all ${
                  cartAnimating ? 'animate-ping' : ''
                }`}>
                  {cartCount}
                </span>
              )}
            </Link>

            {/* Compare Button */}
            <CompareButton lang={lang} dictionary={dictionary} />

            {/* User Menu */}
            <div className="relative">
              <button
                onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                className="p-2 text-gray-700 hover:text-blue-600 transition-colors ml-2"
                aria-label="User menu"
              >
                <svg className={`transition-all ${isScrolled ? 'w-5 h-5' : 'w-6 h-6'}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              </button>

              {/* User Dropdown Menu */}
              {isUserMenuOpen && (
                <div className="absolute right-0 mt-2 w-48 bg-white rounded-md shadow-lg py-1 z-50 border border-gray-200">
                  {isAuthenticated ? (
                    <>
                      <Link
                        href={`/${lang}/account`}
                        className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                        onClick={() => setIsUserMenuOpen(false)}
                      >
                        {dictionary?.auth?.my_account || 'My Account'}
                      </Link>
                      <Link
                        href={`/${lang}/orders`}
                        className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                        onClick={() => setIsUserMenuOpen(false)}
                      >
                        {dictionary?.auth?.my_orders || 'My Orders'}
                      </Link>
                      <hr className="my-1" />
                      <button
                        onClick={() => {
                          logout();
                          setIsUserMenuOpen(false);
                        }}
                        className="block w-full text-left px-4 py-2 text-sm text-red-600 hover:bg-red-50"
                      >
                        {dictionary?.auth?.logout || 'Logout'}
                      </button>
                    </>
                  ) : (
                    <>
                      <Link
                        href={`/${lang}/login`}
                        className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                        onClick={() => setIsUserMenuOpen(false)}
                      >
                        {dictionary?.auth?.login || 'Login'}
                      </Link>
                      <Link
                        href={`/${lang}/register`}
                        className="block px-4 py-2 text-sm text-blue-600 hover:bg-gray-100 font-semibold"
                        onClick={() => setIsUserMenuOpen(false)}
                      >
                        {dictionary?.auth?.register || 'Register'}
                      </Link>
                    </>
                  )}
                </div>
              )}
            </div>
          </nav>

          {/* CTA Button */}
          <div className="hidden md:block">
            <Link href={`/${lang}/quote`}>
              <Button variant="primary" size={isScrolled ? 'sm' : 'md'}>
                Get Started
              </Button>
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden p-2 text-gray-700 hover:text-blue-600 transition"
            onClick={(e) => {
              e.stopPropagation();
              setIsMenuOpen(!isMenuOpen);
            }}
          >
            <svg className={`transition-all ${isScrolled ? 'w-5 h-5' : 'w-6 h-6'}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
              {isMenuOpen ? (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              ) : (
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
              )}
            </svg>
          </button>
        </div>

        {/* Mobile Menu */}
        {isMenuOpen && (
          <nav className="md:hidden mt-4 pb-4 border-t border-gray-100 pt-4" onClick={(e) => e.stopPropagation()}>
            <div className="flex flex-col gap-4">
              {menuItems.map((item) => renderMenuItem(item, true))}
              
              {/* Mobile Cart Icon */}
              <Link
                href={`/${lang}/cart`}
                className={`flex items-center gap-2 p-2 text-gray-700 hover:text-blue-600 transition-all ${
                  cartAnimating ? 'animate-bounce' : ''
                }`}
                onClick={() => setIsMenuOpen(false)}
              >
                <svg className={`w-6 h-6 ${cartAnimating ? 'animate-spin' : ''}`} fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
                </svg>
                <span>Cart</span>
                {cartCount > 0 && (
                  <span className={`bg-red-600 text-white text-xs font-bold rounded-full px-2 py-0.5 transition-all ${
                    cartAnimating ? 'animate-ping' : ''
                  }`}>
                    {cartCount}
                  </span>
                )}
              </Link>

              {/* Mobile User Menu */}
              <div className="border-t border-gray-100 pt-4 mt-4">
                <div className="text-xs font-semibold text-gray-500 uppercase tracking-wider mb-2">
                  Account
                </div>
                {isAuthenticated ? (
                  <>
                    <Link
                      href={`/${lang}/account`}
                      className="flex items-center gap-2 p-2 text-gray-700 hover:text-blue-600 transition-colors"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                      </svg>
                      <span>{dictionary?.auth?.my_account || 'My Account'}</span>
                    </Link>
                    <Link
                      href={`/${lang}/orders`}
                      className="flex items-center gap-2 p-2 text-gray-700 hover:text-blue-600 transition-colors"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
                      </svg>
                      <span>{dictionary?.auth?.my_orders || 'My Orders'}</span>
                    </Link>
                    <button
                      onClick={() => {
                        logout();
                        setIsMenuOpen(false);
                      }}
                      className="flex items-center gap-2 p-2 text-red-600 hover:text-red-700 transition-colors w-full text-left"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 16l4-4m0 0l-4-4m4 4H7m6 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h4a3 3 0 013 3v1" />
                      </svg>
                      <span>{dictionary?.auth?.logout || 'Logout'}</span>
                    </button>
                  </>
                ) : (
                  <>
                    <Link
                      href={`/${lang}/login`}
                      className="flex items-center gap-2 p-2 text-gray-700 hover:text-blue-600 transition-colors"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 16l-4-4m0 0l4-4m-4 4h14m-5 4v1a3 3 0 01-3 3H6a3 3 0 01-3-3V7a3 3 0 013-3h7a3 3 0 013 3v1" />
                      </svg>
                      <span>{dictionary?.auth?.login || 'Login'}</span>
                    </Link>
                    <Link
                      href={`/${lang}/register`}
                      className="flex items-center gap-2 p-2 text-blue-600 hover:text-blue-700 transition-colors font-semibold"
                      onClick={() => setIsMenuOpen(false)}
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M18 9v3m0 0v3m0-3h3m-3 0h-3m-2-5a4 4 0 11-8 0 4 4 0 018 0zM3 20a6 6 0 0112 0v1H3v-1z" />
                      </svg>
                      <span>{dictionary?.auth?.register || 'Register'}</span>
                    </Link>
                  </>
                )}
              </div>

              <Link href={`/${lang}/quote`} className="block">
                <Button variant="primary" size="md" fullWidth>
                  Get Started
                </Button>
              </Link>
            </div>
          </nav>
        )}
        </div>
      </header>
      </div>

      {/* Spacer to account for fixed header */}
      <div className="h-28"></div>
    </>
  );
}
