'use client';

import { useState, useEffect, useRef } from 'react';
import { MessageCircle } from 'lucide-react';
import { SingleChatButton } from './SingleChatButton';
import { getChatConfig, getEnabledPlatforms, type ChatConfig } from '@/lib/config/chatConfig';
import { useChatState } from '@/lib/context/ChatContext';

interface ChatButtonsProps {
  dictionary?: {
    chat?: {
      tooltip: string;
      title: string;
      close: string;
    };
  };
}

export function ChatButtons({ dictionary }: ChatButtonsProps) {
  const [config, setConfig] = useState<ChatConfig | null>(null);
  const [mounted, setMounted] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { isChatOpen, setIsChatOpen } = useChatState();
  const containerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    setMounted(true);

    // Fetch config from WordPress
    getChatConfig()
      .then(setConfig)
      .catch((err) => {
        console.error('[ChatButtons] Error loading config:', err);
        setError(err.message);
      });
  }, []);

  // Close when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (containerRef.current && !containerRef.current.contains(event.target as Node)) {
        setIsOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => document.removeEventListener('mousedown', handleClickOutside);
  }, []);

  const toggleChat = () => {
    setIsChatOpen(!isChatOpen);
  };

  if (!mounted) return null;
  if (error) {
    console.error('[ChatButtons] Error:', error);
    return null;
  }
  if (!config) {
    console.log('[ChatButtons] No config loaded yet');
    return null;
  }

  const enabledPlatforms = getEnabledPlatforms(config);
  console.log('[ChatButtons] Enabled platforms:', enabledPlatforms);

  if (enabledPlatforms.length === 0) {
    console.log('[ChatButtons] No enabled platforms');
    return null;
  }

  return (
    <div ref={containerRef} className="fixed right-8 bottom-8 z-[9999] flex flex-col items-end gap-3">
      {/* Platform buttons - show when expanded */}
      {isChatOpen && enabledPlatforms.map((platform, index) => (
        <div
          key={platform.id}
          className={`animate-slideInRight stagger-${index + 1}`}
        >
          <SingleChatButton
            platform={platform}
            config={config}
            dictionary={dictionary}
            index={index}
            isExpanded={isChatOpen}
          />
        </div>
      ))}

      {/* Main toggle button */}
      <button
        onClick={toggleChat}
        className={`group relative bg-gradient-to-br from-green-500 to-green-600 hover:from-green-600 hover:to-green-700 text-white p-4 rounded-full shadow-lg hover:shadow-2xl transition-all duration-300 transform hover:scale-110 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-green-500 ${
          isChatOpen ? 'rotate-45' : ''
        }`}
        aria-label={isChatOpen ? 'Close chat' : 'Open chat'}
      >
        {/* Chat icon */}
        <MessageCircle className="w-6 h-6" />

        {/* Notification dot - only show when closed */}
        {!isChatOpen && (
          <span className="absolute -top-1 -right-1 w-4 h-4 bg-red-500 rounded-full border-2 border-white animate-bounce"></span>
        )}

        {/* Tooltip when closed */}
        {!isChatOpen && (
          <div className="absolute right-full mr-3 top-1/2 -translate-y-1/2 whitespace-nowrap bg-gray-900 text-white px-3 py-2 rounded-lg text-sm opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none">
            Chat with us!
            <div className="absolute top-1/2 -translate-y-1/2 -right-1 w-2 h-2 bg-gray-900 transform rotate-45"></div>
          </div>
        )}

        {/* Hover glow */}
        <div className="absolute inset-0 rounded-full bg-green-400 opacity-0 group-hover:opacity-30 blur-xl transition-opacity duration-300"></div>
      </button>
    </div>
  );
}
